/* global module */
/**
 * Enumeration of the audio types
 * @type {{MIC: string, DESKTOP: string, NONE: string}}
 */
const AudioType = {
    /**
     * The mic audio type.
     */
    MIC: 'mic',

    /**
     * The desktop audio type.
     */
    DESKTOP: 'desktop',

    /**
     * No local audio source.
     */
    NONE: 'none'
};

module.exports = AudioType;
